import React from 'react'
import './index.less'
import {Select, Form,Upload, Modal, Button, InputNumber, message, DatePicker, notification, Icon,Radio,Input} from 'antd';
import axios from "axios";
import {baseUrl} from "../../../config";
import moment from 'moment'
const FormItem = Form.Item;
const {RangePicker} = DatePicker;
const { TextArea } = Input;
const { Option } = Select

function getBase64(file) {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = error => reject(error);
    });
}

class UploadQuestionForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id_senting_work:'',
            // 文件
            previewVisible: false,
            previewImage: '',
            fileList:[],
        }
    }
    // 文件上传
    handleCancel = () => this.setState({ previewVisible: false });

    handlePreview = async file => {
        if (!file.url && !file.preview) {
            file.preview = await getBase64(file.originFileObj);
        }

        this.setState({
            previewImage: file.url || file.preview,
            previewVisible: true,
        });
    };

    handleChange = ({ fileList }) => this.setState({ fileList });

    componentDidMount() {
        // 获取号
        axios.get(
            baseUrl + '/api/get_senting_work_id',
            {
                params: {
                    token: localStorage.customer_token,
                }
            })
            .then((Response) => {
                console.log('返回数据',Response.data);
                this.setState({
                    id_senting_work: Response.data.id,
                })
            })
    }
    handleSubmit = ()=>{
        // 获取所有的值
        let allVals= this.props.form.getFieldsValue()
        this.props.form.validateFields((err,values)=>{
            if (!err){
                console.log('表单值', values);
                console.log({
                    'work_name': values.work_name,    //*
                    'work_tpl': values.work_tpl,  //任务类型*
                    'work_money': values.work_money,  //每个工作者的任务报酬*
                    'work_person_number': values.work_person_number,    //工作者人数*
                    'work_time': moment().unix(),   //发布时间*
                    'work_content': values.work_content,    //工作描述*
                    'stop_at': values.stop_at.unix(), //报名截止时间*
                    'token': localStorage.customer_token,//*
                    'id_senting_work': this.state.id_senting_work,   //get_senting_work_id返回的id*
                    'work_url': values.work_url,    //任务链接
                    'file_num': this.state.fileList.length,  //已上传的图片数
                    'begin_work_time': values.begin_work_time.unix(),  //工作开始时间*
                    'stop_work_time': values.stop_work_time.unix(),   //工作结束时间*
                    'id_communication': 0,
                })
                // 发送
                axios.get(
                    baseUrl + '/api/sent_work',
                    {
                        params: {
                            'work_name': values.work_name,    //*
                            'work_tpl': values.work_tpl,  //任务类型*
                            'work_money': values.work_money,  //每个工作者的任务报酬*
                            'work_person_number': values.work_person_number,    //工作者人数*
                            'work_time': moment().unix(),   //发布时间*
                            'work_content': values.work_content,    //工作描述*
                            'stop_at': values.stop_at.unix(), //报名截止时间*
                            'token': localStorage.customer_token,//*
                            'id_senting_work': this.state.id_senting_work,   //get_senting_work_id返回的id*
                            'work_url': JSON.stringify(values.work_url),    //任务链接
                            'file_num': this.state.fileList.length,  //已上传的图片数
                            'begin_work_time': values.begin_work_time.unix(),  //工作开始时间*
                            'stop_work_time': values.stop_work_time.unix(),   //工作结束时间*
                            'id_communication': 0,
                        }
                    })
                    .then((Response) => {
                        console.log(Response.data);
                        const d = Response.data
                        if (d.success){
                            notification.open({
                                message: d.success,
                                description: '正在自动跳转',
                                placement: 'bottomLeft',
                                icon: <Icon type="smile" style={{ color: '#108ee9' }} />,
                            });
                            setTimeout(()=>{
                                document.location = `#/about/TaskPosted`
                            },1000)

                        }else {
                            message.error(d.sorry)
                        }
                    })
            }
        })
        console.log(allVals)
    }
    render() {
        const {getFieldDecorator} = this.props.form;
        const { previewVisible, previewImage, fileList } = this.state;
        const uploadButton = (
            <div>
                <Icon type="plus" />
                <div className="ant-upload-text">Upload</div>
            </div>
        );
        return (
            <div>
                {/*<Card title='发布任务'>*/}
                <h2>发布任务</h2>
                    <Form>
                        <FormItem label='任务类型'>
                            {getFieldDecorator('work_tpl',{
                                initialValue:'1',
                            })
                            (
                                <Select>
                                    <Option value="1">问卷类</Option>
                                    <Option value="2">转发类</Option>
                                    <Option value="3">代办类</Option>
                                    <Option value="0">其他</Option>
                                </Select>
                            )
                            }
                        </FormItem>
                        <FormItem label='任务名'>
                            {getFieldDecorator('work_name',{
                                initialValue:'',
                                rules:[{
                                        required:true,
                                        message:'任务名不能为空'
                                }]
                                })
                            (<Input placeholder='任务名'/>)
                            }
                        </FormItem>
                        <FormItem label='报酬'>
                            {getFieldDecorator('work_money',{
                                initialValue:'',
                                rules:[{
                                        required:true,
                                        message:'报酬不能为空'
                                }]
                                })
                            (<Input placeholder='报酬'/>)
                            }
                        </FormItem>
                        <FormItem  label='征集人数'>
                            {getFieldDecorator('work_person_number',{
                                initialValue:'',
                                rules:[{
                                    required:true,
                                    message:'人数不能为空'
                                }]
                            })
                            (<InputNumber placeholder='征集人数'/>)
                            }
                        </FormItem>
                        <FormItem  label='任务开始时间'>
                            {getFieldDecorator('begin_work_time',{
                                initialValue:moment(),
                            })
                            (<DatePicker showTime format='YYYY-MM-DD HH:mm'/>)
                            }
                        </FormItem>
                        <FormItem  label='任务结束时间'>
                            {getFieldDecorator('stop_work_time',{
                                initialValue:moment(),
                            })
                            (<DatePicker showTime format='YYYY-MM-DD HH:mm'/>)
                            }
                        </FormItem>
                        <FormItem  label='报名截止时间'>
                            {getFieldDecorator('stop_at',{
                                initialValue:moment(),
                            })
                            (<DatePicker showTime format='YYYY-MM-DD HH:mm'/>)
                            }
                        </FormItem>

                        <FormItem  label='任务描述(选填)'>
                            {getFieldDecorator('work_content')
                            (<TextArea placeholder='任务描述'/>)
                            }
                        </FormItem>
                        <FormItem  label='任务相关链接(选填)'>
                            {getFieldDecorator('work_url',)
                            (<Input placeholder='任务相关链接'/>)
                            }
                        </FormItem>
                        <FormItem  label='任务相关图片说明(选填)'>

                        <div>
                            <Upload
                                action={baseUrl + '/api/upload_work_file'}
                                listType="picture-card"
                                fileList={fileList}
                                onPreview={this.handlePreview}
                                onChange={this.handleChange}
                                data={{
                                    token: localStorage.customer_token,
                                    id_work: this.state.id_senting_work}}
                            >
                                {fileList.length >= 3 ? null : uploadButton}
                            </Upload>
                            <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
                                <img alt="example" style={{ width: '100%' }} src={previewImage} />
                            </Modal>
                        </div>
                        </FormItem>


                        {/*<FormItem label='任务时间'>*/}
                        {/*    {getFieldDecorator('workRange',{*/}
                        {/*        rules:[{*/}
                        {/*                required:true,*/}
                        {/*                message:'不能为空'*/}
                        {/*        }]*/}
                        {/*        })*/}
                        {/*    (<RangePicker size='small' showTime format='YYYY-MM-DD HH:mm'/>)*/}
                        {/*    }*/}
                        {/*</FormItem>*/}
                        <FormItem>
                            <Button onClick={this.handleSubmit}>发布</Button>
                        </FormItem>
                    </Form>
                {/*</Card>*/}
            </div>
        )
    }
}
export const UploadQuestion = Form.create()(UploadQuestionForm);